$myBlue:  #185b64;
$myGreen: #006400;
$myBlack: #404040;

header {
    background-image: url('./img/nature2-small.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    padding: 20px 0;
    text-align: center;
    text-shadow: 1px 1px $myBlue;
    color: white;
    font-size: 1em;
    font-weight: bold;
    justify-content: center;
    align-items: center;

    h1 {
        font-size: 1.8em;
    }
    
    form{
        margin: 1em 30px;
        h4 {
            font-size: 1.3em;
        }
        img{
            height: 15px;
            width: 15px;
            margin-right: 10px;
        }
        .btn {
            height: 30px;
            width: 30px;
            border-radius: 2em;
            border: none;
            margin: 5px;
            padding: 0;
            box-shadow: 0.5px 0.5px $myBlue;
            img {
                margin: 0;
                height: 20px;
                width: 20px;
            }
        }
    }

    .filters{
        margin-top: 0;
    }
    
    #searchBox {
        opacity: 0.8;
        width:100%;
        margin: 0;
    }
    #searchBox:hover {
        opacity: 1;
    }
    
    input {
        border-radius: 2em;
        text-align: center;
        border: none;
        margin: 10px;
        box-shadow: 1px 0.5px $myBlue;
    }
    
    #searchBar {
        width: 75%;
        height: 30px;
    }

    .slider{
        width: 300px;
        margin: auto;
        background-color: rgba(255,255,255,0.5);
        border-radius: 5px;
        padding: 2em;

    }
}

@media (min-width: 576px) {
    header {
        #searchBar {
        width: 50%;
        }
        h1 {
            font-size: 3em;
        }
    }
}
