/* The only rule that matters */
#video-background {
    /*  making the video fullscreen  */
      position: fixed;
      right: 0; 
      bottom: 0;
      min-width: 100%; 
      min-height: 100%;
      width: auto; 
      height: auto;
      z-index: -100;
}

.login{
    margin: 12% 20%;
    margin-bottom: 50%;
    title{
        margin-bottom: 2em;
    }
}

#signOut{
    height: 50px;
    width: 100px;
    margin: 5% 40%;
}

.welcome{
    margin: 5% 25%;
    height: 100%;
}

h1 {
    text-shadow: 2px 2px black;
    letter-spacing: 5px;
    display: block;
}

.text-box {
    background-color: rgba(0,0,0,.7);
    padding: 20px;
}

.userPhoto {
    width:200px;
    height:200px;
    border-radius: 50%;
    margin-right: .5em;
    margin-bottom: .5em;
}

@media (min-width: 576px){
    .login{
        margin-bottom: 45%;
    }
}

@media (min-width: 768px){
    .login{
        margin-bottom: 35%;
    }
}

@media (min-width: 992px){
    .login{
        margin-bottom: 20%;
    }
}